import { Field, InputType } from '@nestjs/graphql';
import { PartialType } from '@nestjs/mapped-types';
import { IsNotEmpty, IsOptional, IsString, IsUUID } from 'class-validator';
import { CreateActionInput } from './create-action.input';
import { Action, Role } from "../../../../orm-config";
import { ColumnExists, RelationExists } from "../../../../validation";
@InputType()
export class UpdateActionInput extends PartialType(CreateActionInput) {
  
  @Field(() => String)
  @IsUUID(4)
  @IsNotEmpty()
  @RelationExists(Action)
  id: string;

  @Field(() => String, { nullable: true })
  @IsString()
  @IsNotEmpty()
  @IsOptional()
  @ColumnExists(Action)
  name: string;
  
  @Field(() => [String], { nullable: true })
  @IsUUID(4, { each: true })
  @RelationExists(Role)
  @IsOptional()
  roleIds: string[];
}
